/* Layout ▸ Modal
----------------------------- */
.modal {
  z-index: 9999;
  background: rgba(0, 0, 0, 0.75) none repeat scroll 0 0;
}

.modal {
  text-align: center;
}

.modal-backdrop.in {
  opacity: 0.95;
  filter: alpha(opacity=95);
}

.modal-intro {
  color: #ffffff;
  text-align: left;
}

.modal.fade .modal-dialog {
  -webkit-transition: -webkit-transform 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.475);
  -o-transition: -o-transform 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.475);
  transition: transform 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.475);
  -webkit-transform: translate(0, -25%);
  /* Chrome, Safari, Opera */
  -moz-transform: translate(0, -25%);
  /* Mozilla Firefox */
  -o-transform: translate(0, -25%);
  -ms-transform: translate(0, -25%);
  /* IE 9 */
  transform: translate(0, -25%);
}

.modal.in .modal-dialog {
  -webkit-transform: translate(0, 0);
  /* Chrome, Safari, Opera */
  -moz-transform: translate(0, 0);
  /* Mozilla Firefox */
  -o-transform: translate(0, 0);
  -ms-transform: translate(0, 0);
  /* IE 9 */
  transform: translate(0, 0);
}

.modal:before {
  content: " ";
  height: 100%;
  display: inline-block;
  vertical-align: middle;
}

.modal-dialog {
  text-align: left;
  display: inline-block;
  vertical-align: middle;
}

/*  */
/* Modal Sizes */
/*  */
.modal-dialog.modal-sm {
  width: 560px;
}

.modal-dialog.modal-md {
  width: 680px;
}

.modal-dialog.modal-lg {
  max-width: 760px;
  width: 90%;
}

.modal-dialog.modal-extlg {
  width: 1100px;
}

/*  */
/* Modal Content */
/*  */
.modal-content {
  border: 0px none;
  -webkit-border-radius: 0px;
  border-radius: 0px;
  -webkit-box-shadow: 0 8px 35px 8px rgba(0, 0, 0, 0.9);
  -moz-box-shadow: 0 8px 35px 8px rgba(0, 0, 0, 0.9);
  -ms-box-shadow: 0 8px 35px 8px rgba(0, 0, 0, 0.9);
  box-shadow: 0 8px 35px 8px rgba(0, 0, 0, 0.9);
}

/*  */
/* Modal Close */
/*  */
.modal-close {
  top: 60px;
  right: 60px;
  z-index: 50;
  position: absolute;
  opacity: 0;
  filter: alpha(opacity=0);
  -webkit-transform: translateY(-40px) rotate(90deg);
  /* Chrome, Safari, Opera */
  -moz-transform: translateY(-40px) rotate(90deg);
  /* Mozilla Firefox */
  -o-transform: translateY(-40px) rotate(90deg);
  -ms-transform: translateY(-40px) rotate(90deg);
  /* IE 9 */
  transform: translateY(-40px) rotate(90deg);
}

.modal.in .modal-close {
  opacity: 1;
  filter: alpha(opacity=100);
  -webkit-transform: translateY(0px) rotate(0deg);
  /* Chrome, Safari, Opera */
  -moz-transform: translateY(0px) rotate(0deg);
  /* Mozilla Firefox */
  -o-transform: translateY(0px) rotate(0deg);
  -ms-transform: translateY(0px) rotate(0deg);
  /* IE 9 */
  transform: translateY(0px) rotate(0deg);
}

.x-close {
  top: -35px;
  right: 0px;
  z-index: 50;
  width: 22px;
  height: 22px;
  border: none;
  cursor: pointer;
  position: absolute;
  background: transparent;
  transition: all 0.3s ease-in;
  -moz-transition: all 0.3s ease-in;
  -o-transition: all 0.3s ease-in;
  -webkit-transition: all 0.3s ease-in;
  -ms-transition: all 0.3s ease-in;
}

.x-close:hover {
  -webkit-transform: translateY(0px) rotate(90deg) !important;
  /* Chrome, Safari, Opera */
  -moz-transform: translateY(0px) rotate(90deg) !important;
  /* Mozilla Firefox */
  -o-transform: translateY(0px) rotate(90deg) !important;
  -ms-transform: translateY(0px) rotate(90deg) !important;
  /* IE 9 */
  transform: translateY(0px) rotate(90deg) !important;
}

.x-close:before, .x-close:after {
  top: 50%;
  left: -15%;
  right: -15%;
  height: 1px;
  content: ' ';
  position: absolute;
  background-color: #ffffff;
  transition: all 0.3s ease-in;
  -moz-transition: all 0.3s ease-in;
  -o-transition: all 0.3s ease-in;
  -webkit-transition: all 0.3s ease-in;
  -ms-transition: all 0.3s ease-in;
}

.x-close:before {
  -webkit-transform: rotate(45deg);
  /* Chrome, Safari, Opera */
  -moz-transform: rotate(45deg);
  /* Mozilla Firefox */
  -o-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  /* IE 9 */
  transform: rotate(45deg);
}

.x-close:after {
  -webkit-transform: rotate(-45deg);
  /* Chrome, Safari, Opera */
  -moz-transform: rotate(-45deg);
  /* Mozilla Firefox */
  -o-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  /* IE 9 */
  transform: rotate(-45deg);
}

